import React, { Component } from "react";
import TweetEmbed from "react-tweet-embed";


import Footer from "./Footer";
import Header from "./Header";
import indonesia from "data/indonesia.json";

class SelebDetail extends Component {
  constructor(props) {
    super(props);
    let data = indonesia;
    // eslint-disable-next-line
    const userDetail = data.filter(item => {
      if (item.username === this.props.match.params.username) return item;
    });

    const options = {
      align: "center",
      theme: "dark"
    };
    this.state = {
      allData: data,
      currentData: userDetail,
      loadedItem: 20,
      isLoading: true,
      options: options
    };
  }

  hideLoading = () => {
    console.log("HEI FINISHED LOADING!!!!");
    this.setState({ isLoading: false });
  };

  renderTweet = (data, callback) => {
    data.map(item => {
      return <TweetEmbed id={item} options={{ align: "center" }} />;
    });
  };

  render() {
    console.log(this.state.currentData);
    return (
      <div className="site">
        <div className="site-content">
          <Header />
          <div className="container">
            <h1 className="App-title is-size-2 has-text-black has-text-centered is-size-5-mobile">
              Top 7 Tweets
            </h1>
            <ul className="leaderboard">
              <li>
                <div id="topSeven">
                  {this.state.currentData[0].top_tweets.map(item => {
                    return (
                      <TweetEmbed id={item} options={{ align: "center" }} />
                    );
                  })}
                </div>
              </li>
            </ul>
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}

export default SelebDetail;



