import React from 'react';
import { render } from 'react-dom';
import App from 'components/App';
import Faq from 'components/Faq';
import SelebDetail from 'components/SelebDetail';
import registerServiceWorker from './registerServiceWorker';
import { BrowserRouter, Route, Switch } from 'react-router-dom';

const Application = () => (
  <BrowserRouter>
    <Switch>
    <Route exact path={process.env.PUBLIC_URL + '/'} component={() => <App region="Indonesia" leaderboardTitle="Indonesia" /> }  /> 
    <Route exact path={process.env.PUBLIC_URL + '/world'} component={() => <App region="World" leaderboardTitle="World" /> }  /> 
    <Route exact path={process.env.PUBLIC_URL + '/football-club'} component={() => <App region="fc" leaderboardTitle="Football Club" /> }  /> 
    <Route exact path={process.env.PUBLIC_URL + '/faq'} component={Faq} />
    <Route path={process.env.PUBLIC_URL + '/detail/:username'} component={SelebDetail} />
    </Switch>
  </BrowserRouter>
)
render(<Application />, document.getElementById('root'));
registerServiceWorker();



