import React, { Component } from "react";

import LeaderboardItem from "./LeaderboardItem";
import indonesia from "data/indonesia.json";
import world from "data/world.json";
import fc from "data/fc.json";
import Footer from "./Footer";
import Header from "./Header";

class App extends Component {
  constructor(props) {
    super(props);

    let data = [];
    const region = this.props.region;
    if (region === "Indonesia") {
      data = indonesia
    } else if (region === "fc") {
      data = fc
    } else {
      data = world
    }

    this.state = {
      allData: data,
      currentData: data,
      loadedItem: 20,
      isLoading: false
    };
  }

  render() {
    return (
      <div className="site">
        <div className="site-content">
          <Header />
          <div className="container">
            <h1 className="App-title is-size-2 has-text-black has-text-centered">
              <span className="icon">
                <img src="assets/twitter_circle_logo.svg" alt="#SSA" />
              </span>
              <strong> Selebtwit </strong>
              {this.props.region === "Indonesia" ? (
                <span className="icon flag">
                  <img src="assets/id.svg" alt="Indonesia" />
                </span>
              ) : (
                <span className="icon flag">
                  <img src="assets/globe.svg" alt="World" />
                </span>
              )}
            </h1>
            <ul className="leaderboard">
              <li key="search-box" className="box is-marginless has-text-centered is-shadowless field has-addons is-fixed-top">
                <div className="control is-expanded">
                  <input
                    className="input is-expanded"
                    type="text"
                    placeholder="Cari @Username"
                    id="search"
                    onChange={this.handleSearch}
                  />
                </div>
              </li>
              {this.state.currentData
                .slice(0, this.state.loadedItem)
                .map((selebtwit, index) => {
                  return <LeaderboardItem {...selebtwit} />;
                })}
              {this.state.loadedItem > this.state.allData.length ||
              this.state.currentData.length < 20 ||
              this.state.currentData.length < this.state.loadedItem ? (
                ""
              ) : (
                <li key="load_button" className="load-button has-text-centered is-shadowless">
                  <a className="button is-info" onClick={this.handleLoadMore}>
                    Load More
                  </a>
                </li>
              )}
            </ul>
          </div>
        </div>
        <Footer />
      </div>
    );
  }

  handleLoadMore = e => {
    e.preventDefault();
    let loadedItem = this.state.loadedItem;
    let newLoaded = loadedItem + 20;
    console.log("Click Load More Button! with end");
    this.setState({
      loadedItem: newLoaded
    });
  };

  handleSearch = e => {
    e.preventDefault();
    var term = e.target.value;
    var re = new RegExp(term, "ig");

    let data = this.state.allData.filter((seleb, index) => {
      return seleb.username.match(re);
    });

    this.setState({
      currentData: data
    });

    if (term.length < 1) {
      console.log("Zero term");
      this.setState({
        loadedItem: 20
      });
    }
  };
}

export default App;



