import React, { Component } from "react";
import StatisticItem from "./StatisticItem";
import TopSevenTweets from "./TopSevenTweet"

class LeaderboardItem extends Component {
  render() {
    return (
      <li key={this.props.rank} className="box is-marginless">
        <nav className="level">
          <article className="media level-left">
            <div className="media-left">
              <figure className="image is-64x64">
                <img src={this.props.profileImage} alt={this.props.username} />
              </figure>
            </div>
            <div className="media-content">
              <div className="content">
                <p>
                  <strong>
                    #{this.props.rank}. {this.props.name}
                  </strong>
                  {this.props.verified ? (
                    <span className="icon">
                      <img
                        src="assets/twitter-verified-icon.svg"
                        width="13 "
                        height="13"
                        alt={this.props.username}
                      />
                    </span>
                  ) : (
                    " "
                  )}
                  <small className="Username">
                    <a href={"https://twitter.com/" + this.props.username}>
                      {this.props.username}
                    </a>
                  </small>
                {/* <TopSevenTweets username={this.props.username} /> */}
                </p>
                <div>
               
                </div>
              </div>
            </div>
          </article>
          <div className="level-right desktop-stats">
            <StatisticItem heading={"Followers"} value={this.props.followers} />
            <StatisticItem heading={"Tweets"} value={this.props.tweets} />
            <StatisticItem heading={"Total RT"} value={this.props.totalRT} />
            <StatisticItem
              heading={"Total Likes"}
              value={this.props.totalLikes}
            />
            <StatisticItem heading={"Score"} value={this.props.score} />
          </div>

          <div className="level-right mobile-stats">
            <div className="columns is-mobile">
              <div className="column">
                <StatisticItem
                  heading={"Followers"}
                  value={this.props.followers}
                />
              </div>
              <div className="column">
                <StatisticItem heading={"Tweets"} value={this.props.tweets} />
              </div>
            </div>

            <div className="columns is-mobile">
              <div className="column">
                <StatisticItem
                  heading={"Total RT"}
                  value={this.props.totalRT}
                />
              </div>
              <div className="column">
                <StatisticItem
                  heading={"Total Likes"}
                  value={this.props.totalLikes}
                />
              </div>
            </div>
            <div className="column column-cyan">
              <StatisticItem heading={"Score"} value={this.props.score} />
            </div>
          </div>
        </nav>
      </li>
    );
  }
}

export default LeaderboardItem;



